const Uen_Validate = () => {

    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    // Form
    const [uenForm, setUenForm] = useState({
        uen: ""
    });



    function handleChange(e) {
        e.preventDefault();
        setUenForm(e.target.value);
    };
    function handleKeypress(e) {
        // e.preventDefault();

        //it triggers by pressing the enter key
        if (e.keyCode === 13) {
            var validate_btn = document.getElementById('validate-btn');
            validate_btn.click();

            // console.log(e);
        }
    };
    const onSubmit = data => console.log(data);

    return (
        <Fragment>
            <Container>
                <h1 className="mt-5">Validate UEN</h1>
                <Card className="w-card pb-5">
                    <Card.Body >
                        <Card.Title>Search UEN</Card.Title>
                        <Form onSubmit={handleSubmit(onSubmit)}>
                            <InputGroup className="mb-3" style={{ width: '50%', margin: 'auto' }} >
                                <Form.Control
                                    {...register("uen", { required: true })}

                                    onChange={handleChange}
                                    onKeyPress={handleKeypress}
                                    type="text"
                                    placeholder="Please Enter UEN:"
                                    name="uen"
                                />
                                <Button variant="outline-secondary" id="validate-btn" type="submit">
                                    Validate
                                </Button>
                            </InputGroup>
                        </Form>
                    </Card.Body>
                </Card>
            </Container>
        </Fragment>
    );
}

export default Uen_Validate;
